import { QueryKey, useMutation, useQuery } from "react-query";
import { Task } from "types/task";
import { useAddConfig, useDeleteConfig, useEditConfig, useReorderTaskConfig } from "utils/use-optimistic-options";
import { useHttp } from "./http";
import { useDebounce } from "utils/index";
import { SortProps } from "utils/kanban";

export const useTasks = (param?: Partial<Task>) => {
  const client = useHttp();
  const debouncedName = { ...param, name: useDebounce(param?.name, 200) };
  return useQuery<Task[]>(["tasks", debouncedName], () =>
    client("tasks", { data: debouncedName })
  );
};

export const useAddTask = (queryKey: QueryKey) => {
  const client = useHttp();

  return useMutation(
    (params: Partial<Task>) =>
      client(`tasks`, {
        data: params,
        method: "POST"
      }),
    useAddConfig(queryKey)
  );
};

export const useEditTask = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation(
    (param: Partial<Task>) =>
      client(`tasks/${param.id}`, {
        method: "PATCH",
        data: param
      }),
    useEditConfig(queryKey)
  );
};

export const useDeleteTask = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation(
    ({ id }: { id: number }) =>
      client(`tasks/${id}`, {
        method: "DELETE"
      }),
    useDeleteConfig(queryKey)
  );
};

export const useTask = (id: number) => {
  const client = useHttp();
  return useQuery<Task>(
    ["task", { id }],
    () => client(`tasks/${id}`),
    {
      enabled: !!id
    }
  );
};

export const useReorderTask = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation(
    (params: SortProps) => {
      return client("tasks/reorder", {
        data: params,
        method: "POST"
      });
    },
    useReorderTaskConfig(queryKey)
  );
};
